:root {
  --font-family: "Jost", sans-serif;
  --content-width: 1160px;
  --content-fluid-width: 1790px;
  --container-offset: 15px;
  --container-width: calc(var(--content-width) + (var(--container-offset) * 2));
  --container-fluid-width: calc(var(--content-fluid-width) + (var(--container-offset) * 2));
  --color-light: #fff;
  --color-red: #d52027;
  --color-dark: #141024;
  --color-text: rgba(20, 16, 36, 0.7);
}

/* stylelint-disable */ /* stylelint-disable */ /* stylelint-disable */
.custom-checkbox__field:checked + .custom-checkbox__content::after {
  opacity: 1;
}

.custom-checkbox__field:focus + .custom-checkbox__content::before {
  outline: 2px solid #f00;
  outline-offset: 2px;
}

.custom-checkbox__field:disabled + .custom-checkbox__content {
  opacity: 0.4;
  pointer-events: none;
}

/* stylelint-disable */ /* stylelint-disable */
@font-face {
  font-family: "Jost";
  src: url("../fonts/../fonts/JostRegular.woff2") format("woff2");
  font-weight: 400;
  font-display: swap;
  font-style: normal;
}
@font-face {
  font-family: "Jost";
  src: url("../fonts/../fonts/JostSemiBold.woff2") format("woff2");
  font-weight: 600;
  font-display: swap;
  font-style: normal;
}
html {
  box-sizing: border-box;
}

*,
*::before,
*::after {
  box-sizing: inherit;
}

.page {
  height: 100%;
  font-family: var(--font-family, sans-serif);
  -webkit-text-size-adjust: 100%;
}

.page__body {
  margin: 0;
  min-width: 320px;
  min-height: 100%;
  font-size: 16px;
}

img {
  height: auto;
  max-width: 100%;
  object-fit: cover;
}

a {
  text-decoration: none;
}

.site-container {
  overflow: hidden;
}

.is-hidden {
  display: none !important; /* stylelint-disable-line declaration-no-important */
}

.btn-reset {
  border: none;
  padding: 0;
  background: transparent;
  cursor: pointer;
}

.list-reset {
  list-style: none;
  margin: 0;
  padding: 0;
}

.input-reset {
  -webkit-appearance: none;
  appearance: none;
  border: none;
  border-radius: 0;
  background-color: #fff;
}
.input-reset::-webkit-search-decoration, .input-reset::-webkit-search-cancel-button, .input-reset::-webkit-search-results-button, .input-reset::-webkit-search-results-decoration {
  display: none;
}

.visually-hidden {
  position: absolute;
  overflow: hidden;
  margin: -1px;
  border: 0;
  padding: 0;
  width: 1px;
  height: 1px;
  clip: rect(0 0 0 0);
}

.container {
  margin: 0 auto;
  padding: 0 var(--container-offset);
  max-width: var(--container-width);
}

.container-fluid {
  margin: 0 auto;
  padding: 0 var(--container-offset);
  max-width: var(--container-fluid-width);
}

.js-focus-visible :focus:not(.focus-visible) {
  outline: none;
}

.centered {
  text-align: center;
}

.dis-scroll {
  position: fixed;
  left: 0;
  top: 0;
  overflow: hidden;
  width: 100%;
  height: 100vh;
  overscroll-behavior: none;
}

.page--ios .dis-scroll {
  position: relative;
}

.section-offset {
  padding: 120px 0;
}

.btn {
  display: inline-flex;
  align-items: center;
  justify-content: center;
  border-radius: 5px;
  padding: 25px 30px;
  min-width: 260px;
  font-weight: 400;
  font-size: 18px;
  line-height: 160%;
  text-align: center;
  color: var(--color-light);
  background-color: var(--color-red);
}
.btn--dark {
  background-color: var(--color-dark);
}

.section-title {
  margin: 0;
  margin-top: 120px;
  margin-bottom: 60px;
  font-weight: 600;
  font-size: 50px;
  line-height: 160%;
  color: var(--color-dark);
}
@media (max-width: 1200px) {
  .section-title {
    text-align: center;
  }
}

.tooltip {
  margin-left: 10px;
}
.tooltip__btn {
  display: flex;
  align-items: center;
  justify-content: center;
  border-radius: 100%;
  width: 24px;
  height: 24px;
  background: rgba(20, 16, 36, 0.08);
}
.tooltip__btn svg {
  width: 4px;
  height: 14px;
}
.tooltip__btn:focus + .tooltip__txt {
  opacity: 1;
  visibility: visible;
}
@media (any-hover: hover) {
  .tooltip__btn:hover + .tooltip__txt {
    opacity: 1;
    visibility: visible;
  }
}
.tooltip__txt {
  border-radius: 5px;
  padding: 15px 20px;
  box-shadow: 0 2px 8px rgba(20, 16, 36, 0.1);
  background: var(--color-light);
  font-weight: 400;
  font-size: 16px;
  line-height: 160%;
  text-align: center;
  color: var(--color-dark);
  opacity: 0;
  visibility: hidden;
  transition: opacity 0.3s ease-in-out, visibility 0.3s ease-in-out;
}

.form {
  display: flex;
  flex-direction: column;
  width: 100%;
}
.form__label {
  display: flex;
  flex-direction: column-reverse;
  margin-bottom: 10px;
  width: 100%;
}
.form .just-validate-error-label {
  margin-bottom: 3px;
  color: var(--color-light) !important;
}
.form__input {
  border-radius: 5px;
  padding: 20px;
  width: 100%;
  height: 64px;
  font-weight: 400;
  font-size: 18px;
  line-height: 26px;
  color: var(--color-dark);
}
.form__input::placeholder {
  color: rgba(40, 41, 68, 0.7);
}
.form__btn {
  padding: 18px 30px;
}

.header {
  position: absolute;
  left: 0;
  top: 0;
  padding: 30px 0;
  width: 100%;
}
.header__container {
  display: flex;
  align-items: center;
  justify-content: space-between;
}

.hero {
  padding-top: 160px;
  min-height: 800px;
  background-color: var(--color-dark);
}
.hero__container {
  display: flex;
  align-items: center;
}
.hero__content {
  max-width: 516px;
}
.hero__image {
  transform: translateX(167px);
}
.hero__title {
  margin: 0;
  margin-bottom: 50px;
  font-weight: 600;
  font-size: 70px;
  line-height: 160%;
  text-transform: uppercase;
  color: var(--color-light);
}
.hero__title-text {
  display: block;
  max-width: 90%;
  font-weight: 400;
  font-size: 20px;
  line-height: 160%;
  text-transform: initial;
}
@media (max-width: 1200px) {
  .hero__title {
    font-size: 65px;
  }
}

.nav__list {
  display: flex;
  align-items: center;
}
.nav__item:not(:last-child) {
  margin-right: 40px;
}
.nav__link {
  font-weight: 400;
  font-size: 20px;
  line-height: 160%;
  color: var(--color-light);
}

.phone {
  font-weight: 600;
  font-size: 30px;
  line-height: 160%;
  text-align: right;
  color: var(--color-light);
}

.about {
  background-color: var(--color-light);
}
.about__descr {
  margin: 0;
  margin-bottom: 60px;
  max-width: 892px;
  font-weight: 400;
  font-size: 30px;
  line-height: 160%;
  color: var(--color-dark);
}
.about__descr-hightlight {
  font-weight: 600;
}
@media (max-width: 1200px) {
  .about__descr {
    max-width: none;
  }
}
.about__list {
  display: grid;
  grid-template-columns: repeat(12, 1fr);
  gap: 40px;
}
.about__item {
  grid-column: 3 span;
}
@media (max-width: 1200px) {
  .about__item {
    grid-column: 6 span;
    text-align: center;
  }
}
.about__digit {
  font-weight: 600;
  font-size: 70px;
  line-height: 160%;
  color: var(--color-red);
}
.about__text {
  margin: 0;
  font-weight: 400;
  font-size: 18px;
  line-height: 160%;
  color: var(--color-text);
}

.benefits {
  background-color: #f9f9f9;
}
.benefits .section-title {
  margin-top: 0;
}
.benefits__list {
  display: grid;
  grid-template-columns: repeat(12, 1fr);
  gap: 40px;
}
.benefits__item {
  grid-column: 4 span;
}
@media (max-width: 1200px) {
  .benefits__item {
    grid-column: 6 span;
    text-align: center;
  }
}
.benefits__subtitle {
  display: inline-flex;
  margin: 0;
  margin-bottom: 20px;
  font-weight: 600;
  font-size: 24px;
  line-height: 160%;
  color: var(--color-dark);
}
.benefits__descr {
  margin: 0;
  font-weight: 400;
  font-size: 18px;
  line-height: 160%;
  color: var(--color-text);
}

.trainers__wrapper {
  padding-left: max(15px, (100% - var(--content-width)) / 2);
}
.trainers .swiper-slide {
  max-width: 360px;
}
.trainers .swiper-scrollbar-drag {
  background-color: var(--color-dark);
}
.trainers__image {
  margin-bottom: 20px;
  border-radius: 20px;
  max-height: 500px;
}
.trainers__name {
  margin: 0;
  padding-bottom: 10px;
  font-weight: 600;
  font-size: 24px;
  line-height: 160%;
  color: var(--color-dark);
}
.trainers__post {
  margin: 0;
  padding-bottom: 20px;
  font-weight: 400;
  font-size: 18px;
  line-height: 160%;
  color: var(--color-text);
}
.trainers__btn {
  margin-bottom: 77px;
  font-weight: 400;
  font-size: 18px;
  line-height: 160%;
  color: var(--color-red);
}
.trainers__nav {
  display: flex;
  align-items: center;
  justify-content: space-between;
}
.trainers__scroll {
  border-radius: 40px;
  width: 360px;
  height: 5px;
  background: rgba(20, 16, 36, 0.08);
}
.trainers__arrow-btn {
  background-position: center;
  background-size: cover;
  background-repeat: no-repeat;
  width: 40px;
  height: 40px;
  background-image: url(../img/Arrow.svg);
}
.trainers__arrow-btn--next {
  transform: rotate(180deg);
}
.trainers__arrow-btn:not(:last-child) {
  margin-right: 10px;
}

.tariffs {
  padding-top: 0;
}
.tariffs__list {
  display: grid;
  grid-template-columns: repeat(12, 1fr);
  gap: 40px;
}
.tariffs__item {
  padding: 40px;
  display: flex;
  flex-direction: column;
  align-items: center;
  grid-column: 4 span;
  border: 1px solid rgba(20, 16, 36, 0.2);
  border-radius: 20px;
}
.tariffs__item--current {
  border: 1px solid var(--color-dark);
  background-color: var(--color-dark);
  color: var(--color-light);
  transform: scaleY(1.08);
}
.tariffs__item--current .tariffs__subtitel {
  color: var(--color-light);
}
.tariffs__item--current .tariffs__price {
  color: var(--color-light);
}
.tariffs__item--current .tariffs__descr {
  color: var(--color-light);
}
@media (max-width: 1200px) {
  .tariffs__item {
    transform: scale(1);
  }
}
.tariffs__subtitel {
  display: flex;
  align-items: center;
  margin: 0;
  margin-bottom: 65px;
  font-weight: 600;
  font-size: 30px;
  line-height: 160%;
  color: var(--color-dark);
}
.tariffs__subtitel::before {
  content: "";
  display: block;
  margin-right: 12px;
  width: 20px;
  height: 4px;
  background-color: currentColor;
}
.tariffs__subtitel::after {
  content: "";
  display: block;
  margin-left: 14px;
  width: 20px;
  height: 4px;
  background-color: currentColor;
}
.tariffs__price {
  margin-bottom: 20px;
  font-weight: 600;
  font-size: 50px;
  line-height: 160%;
  text-align: center;
  color: var(--color-dark);
}
@media (max-width: 1200px) {
  .tariffs__price {
    font-size: 45px;
  }
}
.tariffs__descr {
  margin: 0;
  margin-bottom: auto;
  font-weight: 400;
  font-size: 18px;
  line-height: 160%;
  text-align: center;
  color: var(--color-dark);
}
.tariffs__btn {
  margin-top: 60px;
  min-width: 100%;
}

.order {
  background-image: url("../img/percent.png");
  /* stylelint-disable-next-line declaration-block-no-duplicate-properties */
  background-image: image-set("../img/percent.avif" type("image/avif"), "../img/percent.webp" type("image/webp"), "../img/percent.jpg" type("image/jpg"));
  background-color: var(--color-red);
  background-size: cover;
  background-position: bottom center;
}
.order__container {
  padding: 87px 60px;
  display: grid;
  grid-template-columns: repeat(12, 1fr);
}
.order__left {
  grid-column: 5 span;
}
.order__right {
  display: flex;
  justify-content: flex-end;
  grid-column: 7 span;
}
.order__form {
  max-width: 500px;
}
.order__title {
  margin: 0;
  margin-bottom: 20px;
  font-weight: 600;
  font-size: 36px;
  line-height: 130%;
  color: var(--color-light);
}
.order__descr {
  margin: 0;
  font-weight: 400;
  font-size: 20px;
  line-height: 130%;
  color: var(--color-light);
}

.footer {
  padding: 40px 0;
  background-color: var(--color-dark);
}
.footer__container {
  display: flex;
  align-items: flex-start;
  justify-content: space-between;
}
.footer__logo {
  display: inline-block;
  margin-bottom: 20px;
}
.footer__copy {
  font-weight: 400;
  font-size: 16px;
  line-height: 160%;
  color: rgba(255, 255, 255, 0.7);
}